.header-bg {
    background-color: var(--color-12);
}

.header {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: space-between;
    align-items: center;
    padding-top: 20px;
    padding-bottom: 20px;

    a {
        display: flex;
        flex-wrap: wrap;
        align-items: center;

        h1 {
            font-size: 2.5rem;
            font-weight: 700;
            color: var(--color-0);
        }
    }
}

.header-menu {
    display: flex;
    flex-wrap: wrap;
    gap: 40px;
}

.header-menu a {
    display: inline-block;
    padding: 16px 0px;
    position: relative;
}

.header-menu a:after {
    content: "";
    display: block;
    height: 2px;
    width: 0px;
    background: var(--color-0);
    margin-top: 4px;
    transition: 0.3s;
    position: absolute;
}

.header-menu a:hover::after,
.header-menu a.active::after {
    width: 100%;
}

@media (max-width: 800px) {
    .header-menu {
        gap: 20px;
    }
    .header-menu a {
        background: var(--color-11);
        padding: 12px 16px;
        border-radius: 4px;
    }
    .header-menu a:hover {
        background-color: var(--color-10);
    }
    .header a:after {
        display: none;
    }
}

@media (max-width: 600px) {
    .header-menu {
        gap: 12px;
    }
    .header-menu a {
        padding: 8px 12px;
        font-size: 14px;
    }
}
